import PropTypes from 'prop-types';
import React, { memo, useEffect, useState } from 'react';
// eslint-disable-next-line
const loadingGif = require('../../img/lowres_loading.gif');

const ImagePlaceholder = memo(({ className }) => {
  return (
    <img
      className={className}
      src={loadingGif}
      alt="loading..."
    />
  );
});

ImagePlaceholder.propTypes = {
  className: PropTypes.string,
};

const BuchiImage = ({ className, src, alt, handleSignalIsLoaded }) => {
  const [loaded, setLoaded] = useState(false);
  const [isError, setIsError] = useState(false);
  useEffect(() => {
    if (handleSignalIsLoaded) {
      handleSignalIsLoaded();
    }
  }, []);
  const handleImageLoaded = () => {
    setLoaded(true);
  };

  const handleImageError = () => {
    setIsError(true);
  };
  return (
    <div className="image-holder">
        <div style={loaded ? { display: 'none' } : {}}>
          <ImagePlaceholder className={className} />
        </div>
        <img className={className} src={src} alt={alt} style={!(loaded && !isError) ? { display: 'none' } : {}} onLoad={() => handleImageLoaded()} onError={() => handleImageError()} />
        <img className={className} src={'errorImg'} alt={'error-placeholder'} style={!(loaded && isError) ? { display: 'none' } : {}} onLoad={() => handleImageLoaded()} />
    </div>
  );
};

BuchiImage.propTypes = {
  className: PropTypes.string,
  src: PropTypes.string.isRequired,
  alt: PropTypes.string,
  handleSignalIsLoaded: PropTypes.func,
};

export default BuchiImage;
